    .typewriter {
        display       : flex;
        flex-direction: column;
        align-items   : center;
    }

    .typewriter h1 {
        border-right: 1px solid white;
        white-space : nowrap;
        overflow    : hidden;
        width       : 340px;
        white-space : nowrap;
        overflow    : hidden;
        animation   : typewriter 2s steps(25) 0s 1 normal both;
        margin      : 2rem 0 .5rem 0;
        font-size   : 24px;
        text-align  : center;
    }

    .typewriter #line_1 {
        border-right   : 1px solid white;
        white-space    : nowrap;
        overflow       : hidden;
        width          : 130px;
        animation      : typewriter 2s steps(40) 0s 1 normal both;
        animation-delay: 2s;
        margin         : .5rem 0 0 0;
    }

    #line_2 {
        border-right   : 1px solid white;
        white-space    : nowrap;
        overflow       : hidden;
        width          : 131px;
        animation      : typewriter 3s steps(40) 0s 1 normal both;
        animation-delay: 4s;
        margin         : .7rem 0 0 0;

    }

    @keyframes typewriter {
        0% {
            width       : 0;
            border-right: none;
        }

        0.1% {
            border-right: 1px solid rgb(220, 220, 220);
        }

        99.9% {
            border-right: 1px solid rgb(220, 220, 220);
        }

        100% {
            border: none;
        }
    }

    i {
        opacity            : 0;
        animation          : fadeIn 2s ease-in-out;
        animation-delay    : 10s;
        animation-fill-mode: forwards;
        padding-top        : 1rem;
        padding-left       : 20px;

    }

    a {
        color: rgb(220, 220, 220);
    }

    @keyframes fadeIn {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }

    .mainPicture {
        width            : 100%;
        height           : 550px;
        background-image : url('../../assets/forHome.svg');
        background-repeat: no-repeat;
        background-size  : 100% 100%;
    }
