    .navbar {
        height: 50px;
    }

    #as_app ul {
        position               : fixed;
        bottom                 : 0;
        width                  : 100%;
        padding                : 5px;
        min-height             : 50px;
        background             : transparent;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter        : blur(5px);
        z-index                : 3;
        display                : -webkit-box;
        display                : -ms-flexbox;
        display                : flex;
        -webkit-box-align      : center;
        -ms-flex-align         : center;
        align-items            : center;
        -ms-flex-pack          : distribute;
        justify-content        : space-around;
        -webkit-box-shadow     : 0 10px 50px 0 rgb(5 4 62 / 25%);
        box-shadow             : 0 10px 50px 0 rgb(5 4 62 / 25%);
    }

    #as_app #as_app_bar {
        background             : transparent;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter        : blur(5px);
    }

    #as_app ul a {
        position          : relative;
        color             : #000;
        height            : 45px;
        width             : 45px;
        font-size         : 1.25em;
        display           : -webkit-box;
        display           : -ms-flexbox;
        display           : flex;
        -webkit-box-align : center;
        -ms-flex-align    : center;
        align-items       : center;
        -webkit-box-pack  : center;
        -ms-flex-pack     : center;
        justify-content   : center;
        border-radius     : 8px;
        -webkit-transition: 0.5s;
        transition        : 0.5s;
    }

    i[class^="ai_"]:before,
    i[class*=" ai_"]:before {
        font-family            : akar-icons !important;
        font-style             : normal;
        font-weight            : normal !important;
        font-variant           : normal;
        text-transform         : none;
        line-height            : 1;
        -webkit-font-smoothing : antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    @media (min-width:0) and (max-width:279.9px) {
        .typewriter h1 {
            width    : 164px;
            margin   : 2rem 0 .5rem 0;
            font-size: 12px;
        }

        .typewriter #line_1,
        .typewriter #line_2 {
            font-size: 10px;
            width    : 83px;
        }
    }

    @media (min-width:280px) and (max-width:349.9px) {
        .typewriter h1 {
            width    : 250px;
            margin   : 2rem 0 .2rem 0;
            font-size: 18px;
        }

    }

    @media (min-width:0) and (max-width:600px) {
        .mainPicture {
            height: 70vh;
        }
    }